import { RouterProvider } from "react-router-dom";
import { useEffect, useState, Suspense } from "react";
import { generateRoutes } from "./utils/http/generatesRoutes";
import { useDispatch, useSelector } from "react-redux";
import { routes } from "./router";
import { createBrowserRouter } from "react-router-dom";
import { getMenu } from "./api/user";
import { setMenu } from "./store/login/authSlice";
import {Spin} from 'antd'


function App() {
  const { token } = useSelector((state: any) => state.authSlice)
  const [routerss, setRouter] = useState<any>(null)
  const dispatch = useDispatch()
  useEffect(() => {
    async function loadData() {
      const { data } = await getMenu() //getMenu data
      if (data.length) { //
        dispatch(setMenu(data))//redux 
        const routers = generateRoutes(data) //
        const myRoutes = [...routes]
        myRoutes[0].children = routers
        myRoutes[0].children[0].index = true
        const router = createBrowserRouter(myRoutes)
        setRouter(router)
      }else{//
        const router = createBrowserRouter(routes)
        setRouter(router)
      }
    }
    loadData()
  },[token])
  if (routerss) {
    return (
      <div className="App">
        <Suspense fallback={<Spin></Spin>}>
          <RouterProvider router={routerss} />
        </Suspense>

      </div>
    );
  } else {
    return <Spin/>
  }

}

export default App;
